import * as ActivityFormat from './activity';
import * as AddressFormat from './address';
import * as CurrencyFormat from './currency';
import * as DateFormat from './date';
import * as HTMLFormat from './html';
import * as NameFormat from './name';
import * as NumberFormat from './number';
import * as PhoneFormat from './phone';
import * as PickListFormat from './picklist';
import * as StringFormat from './string';

const Format = Object.assign({},
  ActivityFormat,
  AddressFormat,
  CurrencyFormat,
  DateFormat,
  HTMLFormat,
  NameFormat,
  NumberFormat,
  PhoneFormat,
  PickListFormat,
  StringFormat
);

export * from './activity';
export * from './address';
export * from './currency';
export * from './date';
export * from './html';
export * from './name';
export * from './number';
export * from './phone';
export * from './picklist';
export * from './string';

export default Format;



