const nameToPathCache = {};
const nameToPath = function nameToPath(name) {
  if (typeof name !== 'string' || name === '.' || name === '') {
    return []; // '', for compatibility
  }

  if (nameToPathCache[name]) {
    return nameToPathCache[name];
  }

  const parts = name.split('.');
  const path = [];

  for (let i = 0; i < parts.length; i++) {
    const match = parts[i].match(/([a-zA-Z0-9_$]+)\[([^\]]+)]/);
    if (match) {
      path.push(match[1]);
      if (/^\d+$/.test(match[2])) {
        path.push(parseInt(match[2], 10));
      } else {
        path.push(match[2]);
      }
    } else {
      path.push(parts[i]);
    }
  }

  nameToPathCache[name] = path.reverse();
  return nameToPathCache[name];
};

export function memoize(fn, keyFn) {
  const cache = {};
  const _keyFn = keyFn || ((value) => {
    return value;
  });

  return function cached() {
    const key = _keyFn.apply(this, arguments);
    if (cache[key]) {
      return cache[key];
    }

    cache[key] = fn.apply(this, arguments);
    return cache[key];
  };
}

export function getValue(o, name, defaultValue) {
  const path = nameToPath(name).slice(0);
  let current = o;
  while (current && path.length > 0) {
    const key = path.pop();
    if (typeof current[key] !== 'undefined') {
      current = current[key];
    } else {
      return typeof defaultValue !== 'undefined' ? defaultValue : null;
    }
  }
  return current;
}

export function setValue(o, name, val) {
  let current = o;
  const path = nameToPath(name).slice(0);
  while ((typeof current !== 'undefined') && path.length > 1) {
    const key = path.pop();
    const next = path[path.length - 1];
    if (typeof current[key] !== 'undefined') {
      current = current[key] = current[key];
    } else if (typeof next === 'number') {
      current = current[key] = [];
    } else {
      current = current[key] = {};
    }
  }

  if (typeof path[0] !== 'undefined') {
    current[path[0]] = val;
  }

  return o;
}




