/**
 * Sanitizes an Object so that JSON.stringify will work without errors by discarding non-stringable keys.
 * @param {Object} obj Object to be cleansed of non-stringify friendly keys/values.
 * @return {Object} Object ready to be JSON.stringified.
 */
export function sanitizeForJson(obj) {
  let type;

  obj.__visited__ = true;

  for (const key in obj) {
    if (obj.hasOwnProperty(key)) {
      try {
        type = typeof obj[key];
      } catch (e) {
        delete obj[key];
        continue;
      }

      switch (type) {//eslint-disable-line
        case 'undefined':
          obj[key] = 'undefined';
          break;

        case 'function':
          delete obj[key];
          break;

        case 'object':
          if (obj[key] === null) {
            obj[key] = 'null';
            break;
          }
          if (key === 'scope') {
            obj[key] = 'null';
            break;
          }
          // break circular references
          if (obj[key].__visited__) {
            obj[key] = 'null';
            break;
          }
          obj[key] = sanitizeForJson(obj[key]);
          break;
        case 'string':
          try {
            obj[key] = JSON.parse(obj[key]);

            if (typeof obj[key] === 'object') {
              obj[key] = sanitizeForJson(obj[key]);
            }
          } catch (e) {}//eslint-disable-line
          break;
      }
    }
  }

  delete obj.__visited__;
  return obj;
}

export function stripQueryArgs(url = '') {
  const idx = url.indexOf('?');
  if (idx > -1) {
    return url.substr(0, idx);
  }

  return url;
}

export function trimText(text = '', wordCount = 0) {
  const words = text.split(' ');
  if (words.length > wordCount) {
    const intermediate = words.slice(0, wordCount);
    if (intermediate[wordCount - 1].endsWith('.')) {
      intermediate[wordCount - 1] = intermediate[wordCount - 1].slice(0, -1);
    }
    const value = `${intermediate.join(' ')} ...`;
    return value;
  }
  return text;
}

export function escapeSearchQuery(searchQuery) {
  return (searchQuery || '').replace(/"/g, '""');
}



