/** Parses the activity ID
 * @param {string} activityId
 * A string with the activity id seperated by a semi-colon
 * @returns {string}
 */
export function getRealActivityId(activityId) {
  let id = activityId;
  if (activityId) {
    if (activityId.indexOf(';') > 0) {
      id = activityId.substring(0, 12);
    } else {
      id = activityId;
    }
  }
  return id;
}



